<template>
  <v-app id="inspire" style="background-color:#f5f5f5;">
    <v-navigation-drawer
      :clipped="$vuetify.breakpoint.lgAndUp"
      v-model="drawer"
      fixed
      app
    >
      <v-list dense>
        <template v-for="item in items">
          <!-- common dir -->
          <v-list-tile 
            :key="item.text" 
            @click="handleClick(item.path)"
            :ripple="{ class: 'primary--text' }">
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>
                {{ item.text }}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar
      :clipped-left="$vuetify.breakpoint.lgAndUp"
      color="blue darken-3"
      dark
      app
      fixed
    >
      <v-toolbar-title style="width: 300px" class="ml-0 pl-3">
        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <span class="hidden-sm-and-down">Aaron Blog</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon @click="handleClick('https://github.com/guanpengchn')">
        <v-icon>apps</v-icon>
      </v-btn>
    </v-toolbar>
    <v-content>
        <router-view></router-view>
    </v-content>
  </v-app>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    drawer: null,
    items: [
      { icon: "assignment", text: "Article", path: "/articles" },
      {
        icon: "event",
        text: "Note",
        path: "https://github.com/guanpengchn/note"
      },
      {
        icon: "fullscreen",
        text: "Presentation",
        path: "https://github.com/guanpengchn/presentation"
      },
      {
        icon: "rss_feed",
        text: "CSDN",
        path: "https://blog.csdn.net/grape875499765"
      },
      { icon: "av_timer", text: "Design", path: "/design" },
      { icon: "person", text: "About Me", path: "/about" }
    ]
  }),
  props: {
    source: String
  },
  methods: {
    handleClick: function(path) {
      if (path.indexOf("http") != -1) {
        window.open(path, "_blank");
      } else {
        this.$router.push(path);
      }
    }
  }
};
</script>
